// src/components/Button.js
import React from 'react';
import { Link } from 'gatsby';

const Button = ({ children, to }) => (
  <Link to={to}>
    <button className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-700">
      {children}
    </button>
  </Link>
);

export default Button;
